import React, { useState } from "react";
import * as XLSX from "xlsx";

const fileLinks = {
  "JHS 1": {
      Computing: "https://docs.google.com/spreadsheets/d/e/2PACX-1vT1vLO5sPCgUHoU2ke/edit?usp=drivesdk&output=xlsx",
        },
        };

        const Assessment = () => {
          const [data, setData] = useState([]);
            const [selectedClass, setSelectedClass] = useState("");
              const [selectedSubject, setSelectedSubject] = useState("");

                const handleClassChange = (e) => setSelectedClass(e.target.value);
                  const handleSubjectChange = (e) => setSelectedSubject(e.target.value);

                    const normalizeData = (rows) => {
                        const maxCols = Math.max(...rows.map((row) => row.length));
                            return rows.map((row) => {
                                  const filled = Array.from({ length: maxCols }, (_, i) => row[i] || "");
                                        return filled;
                                            });
                                              };

                                                const handleFile = (file) => {
                                                    const reader = new FileReader();
                                                        reader.onload = (e) => {
                                                              const wb = XLSX.read(e.target.result, { type: "binary" });
                                                                    const ws = wb.Sheets[wb.SheetNames[0]];
                                                                          const rows = XLSX.utils.sheet_to_json(ws, { header: 1 });
                                                                                const normalized = normalizeData(rows);
                                                                                      setData(normalized);
                                                                                          };
                                                                                              reader.readAsBinaryString(file);
                                                                                                };

                                                                                                  const handleFromLink = async () => {
                                                                                                      if (!selectedClass || !selectedSubject) return alert("Select class and subject");

                                                                                                          const link = fileLinks[selectedClass]?.[selectedSubject];
                                                                                                              if (!link) return alert("No file found for this selection");

                                                                                                                  const response = await fetch(link);
                                                                                                                      const blob = await response.blob();
                                                                                                                          handleFile(new File([blob], "fetched.xlsx"));
                                                                                                                            };

                                                                                                                              const handleChange = (rowIdx, colIdx, value) => {
                                                                                                                                  const updated = [...data];
                                                                                                                                      updated[rowIdx][colIdx] = value;
                                                                                                                                          setData(updated);
                                                                                                                                            };

                                                                                                                                              const handleDownload = () => {
                                                                                                                                                  const ws = XLSX.utils.aoa_to_sheet(data);
                                                                                                                                                      const wb = XLSX.utils.book_new();
                                                                                                                                                          XLSX.utils.book_append_sheet(wb, ws, "Sheet1");

                                                                                                                                                              const className = selectedClass || "Class";
                                                                                                                                                                  const subjectName = selectedSubject || "Subject";
                                                                                                                                                                      XLSX.writeFile(wb, `${className}_${subjectName}_Assessment.xlsx`);
                                                                                                                                                                        };

                                                                                                                                                                          return (
                                                                                                                                                                              <div style={styles.container}>
                                                                                                                                                                                    <h2 style={styles.header}>Excel Assessment Editor</h2>

                                                                                                                                                                                          <div style={styles.selectRow}>
                                                                                                                                                                                                  <select onChange={handleClassChange} value={selectedClass} style={styles.select}>
                                                                                                                                                                                                            <option value="">Select Class</option>
                                                                                                                                                                                                                      <option>JHS 1</option>
                                                                                                                                                                                                                                <option>JHS 2</option>
                                                                                                                                                                                                                                          <option>JHS 3</option>
                                                                                                                                                                                                                                                    <option>Class 6</option>
                                                                                                                                                                                                                                                              <option>Class 5</option>
                                                                                                                                                                                                                                                                        <option>Class 4</option>
                                                                                                                                                                                                                                                                                  <option>Class 3</option>
                                                                                                                                                                                                                                                                                            <option>Class 2</option>
                                                                                                                                                                                                                                                                                                      <option>Class 1</option>
                                                                                                                                                                                                                                                                                                              </select>

                                                                                                                                                                                                                                                                                                                      <select onChange={handleSubjectChange} value={selectedSubject} style={styles.select}>
                                                                                                                                                                                                                                                                                                                                <option value="">Select Subject</option>
                                                                                                                                                                                                                                                                                                                                          <option>Computing</option>
                                                                                                                                                                                                                                                                                                                                                    <option>Math</option>
                                                                                                                                                                                                                                                                                                                                                              <option>Science</option>
                                                                                                                                                                                                                                                                                                                                                                      </select>

                                                                                                                                                                                                                                                                                                                                                                              <button onClick={handleFromLink} style={styles.button}>Load from Drive</button>
                                                                                                                                                                                                                                                                                                                                                                                      <input
                                                                                                                                                                                                                                                                                                                                                                                                type="file"
                                                                                                                                                                                                                                                                                                                                                                                                          accept=".xlsx,.xls"
                                                                                                                                                                                                                                                                                                                                                                                                                    onChange={(e) => handleFile(e.target.files[0])}
                                                                                                                                                                                                                                                                                                                                                                                                                              style={{ marginLeft: "10px" }}
                                                                                                                                                                                                                                                                                                                                                                                                                                      />
                                                                                                                                                                                                                                                                                                                                                                                                                                            </div>

                                                                                                                                                                                                                                                                                                                                                                                                                                                  {data.length > 0 && (
                                                                                                                                                                                                                                                                                                                                                                                                                                                          <div style={styles.tableWrapper}>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                    <table style={styles.table}>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                <tbody>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              {data.map((row, rowIndex) => (
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              <tr key={rowIndex}>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                {row.map((cell, colIndex) => (
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    <td key={colIndex} style={styles.cell}>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          <input
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  type="text"
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          value={cell}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  onChange={(e) => handleChange(rowIndex, colIndex, e.target.value)}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          style={styles.input}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                />
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    </td>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      ))}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      </tr>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    ))}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                </tbody>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          </table>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    <button onClick={handleDownload} style={styles.downloadBtn}>Download Edited File</button>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            </div>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  )}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      </div>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        );
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        };

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        const styles = {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          container: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              padding: "20px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  fontFamily: "Arial, sans-serif",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      maxWidth: "1000px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          margin: "auto",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              header: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  textAlign: "center",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      selectRow: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          display: "flex",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              gap: "10px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  marginBottom: "20px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      flexWrap: "wrap",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          select: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              padding: "8px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  borderRadius: "5px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      border: "1px solid #ccc",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          button: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              padding: "8px 16px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  backgroundColor: "#2196F3",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      color: "#fff",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          border: "none",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              borderRadius: "5px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  cursor: "pointer",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      tableWrapper: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          overflowX: "auto",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              table: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  width: "100%",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      borderCollapse: "collapse",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          cell: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              border: "1px solid #ccc",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  padding: "5px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      input: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          width: "100%",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              border: "none",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  outline: "none",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      padding: "5px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          downloadBtn: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              marginTop: "15px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  padding: "10px 20px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      backgroundColor: "#4CAF50",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          color: "#fff",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              border: "none",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  borderRadius: "5px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    };

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    export default Assessment;